<template>
	<scroll-view class="home-album" scroll-y='true' @scrolltolower="lower">
		<!-- 轮播图区域 -->
		<view class="album-swiper">
			<swiper autoplay indicator-dots circular>
				<swiper-item v-for="item in banner" :key='item.id'>
					<image :src="item.thumb"></image>
				</swiper-item>
			</swiper>
		</view>

		<!-- 专辑列表区域 -->
		<view v-for="item in album" :key='item.id' class="album-list">
			<navigator class="album-list-content" :url="`/pages/album/album?id=${item.id}`">
				<view class="album-list-left">
					<image mode="aspectFill" :src="item.cover"></image>
				</view>

				<view class="album-list-right">
					<text>{{item.name }}</text>
					<text>{{item.desc | deleteEnter}}</text>
					<view class="album-list-btn">
						<view>关注</view>
					</view>
				</view>
			</navigator>
		</view>

	</scroll-view>
</template>

<script>
	export default {
		data() {
			return {
				params: {
					limit: 30,
					order: 'new',
					skip: 0
				},
				banner: [], //轮播图数据 
				album: [], //专辑列表数据
				hasMore: true
			}
		},
		methods: {
			requestData() {
				this.$request({
					url: 'http://157.122.54.189:9088/image/v1/wallpaper/album',
					data: this.params
				}).then(res => {
					if (res.data.res.album.length === 0) {
						this.hasMore = false
						return
					}
					if (this.banner.length === 0) {
						this.banner = res.data.res.banner
					}
					this.album = [...this.album, ...res.data.res.album]
					console.log(res.data.res.album)
					console.log(this.album)

				})
			},
			lower(e) {
				if (this.hasMore) {
					this.params.skip += this.params.limit
					this.requestData()
				} else {
					uni.showToast({
						title: '没有更多信息了',
						icon: 'none'
					})
				}
				console.log('到底了!')
			},
		},
		filters: {
			deleteEnter(value) {
				return value.replace(/[\r\n]/g, '')
			}
		},
		mounted() {
			//页面加载完后修改头部标题
			uni.setNavigationBarTitle({
				title: '专辑'
			})
			this.requestData()
		}
	}
</script>

<style scoped lang="scss">
	.home-album {
		height: auto;
		height: calc(100vh-36px);
	}

	.album-swiper {
		swiper {
			height: calc(750rpx/2.3)
		}

		swiper-item {
			width: 100%;
			height: 100%;

			image {
				width: 100%;
				height: 100%;
			}
		}

	}

	.album-list {
		.album-list-content {
			display: flex;
			padding: 20rpx 20rpx 0 20rpx;
			overflow: hidden;

			.album-list-left {
				flex: 1;

				image {
					width: 200rpx;
					height: 200rpx;
				}
			}

			.album-list-right {
				flex: 2;
				position: relative;
				display: flex;
				padding-left: 20rpx;
				flex-direction: column;
				justify-content: flex-start;
				align-items: flex-start;
				font-size: 22rpx;
				color: #808080;
				overflow: hidden;

				text {
					text-overflow: ellipsis;
					overflow: hidden;
					white-space: nowrap;
					padding: 0 0 25rpx 0;
					width: 100%;
				}

				text:first-child {
					font-size: 28rpx;
					color: #333333;
				}

				.album-list-btn {
					width: 100%;
					display: flex;
					justify-content: flex-end;
					align-items: flex-end;

					view {
						border: 1rpx solid deeppink;
						padding: 8rpx;
						border-radius: 5rpx;
						font-size: 28rpx;
						background: pink;
					}
				}
			}
		}
	}
</style>
